<template>
<v-container>
  <v-layout row wrap v-if="loading">
    <v-flex xs 12 class="text-xs-center">
      <v-progress-circular
          indeterminate
          class="primary--text"
          :width="7"
          :size="70"></v-progress-circular>
      </v-flex>
    </v-layout>
    <v-layout row wrap v-else>
    <v-flex xs12>
      <v-card>
      <v-card-title>
      <h5 class="primary--text">{{ livestock.stockName}}</h5>
<template>
  <v-spacer></v-spacer>
  <app-edit-livestock-details-dialog> :livestock="livestock"</app-edit-livestock-details-dialog>
</template>
</v-card-title>
<v-card-text>
      <div>{{ livestock.breed}}</div>
      <div> {{livestock.age}}</div>
      <div>{{ livestock.numInStock}}</div>
      <div>{{ livestock.date}}</div>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn class="primary">Book appointment for Livestock</v-btn>
</v-card-actions>
    </v-card>
    </v-flex>
  </v-layout>
</v-container>
</template>

<script>
export default {
  props: ['id'],
  computed: {
    livestock () {
      return this.$store.getters.loadOneLivestock(this.id)
    },
    loading () {
      return this.$store.getters.loading
    }
  }
}
</script>



